import * as React from "react";
import { Flex, Text, Box, Input, Button, useMediaQuery, Spinner } from "@chakra-ui/react";
import styles from "./login.module.css";
import { useNavigate } from "react-router-dom";

import { dispatchNumber } from "../../utils";

export default function Login({ number: phone, setNumber: setPhone }){
    const [isSmall] = useMediaQuery("(max-width: 600px)");
    const [isLoading, setIsLoading] = React.useState(false);

    const navigate = useNavigate();

    const handleChange = (event) => setPhone(event.target.value);

    const getCode = async () => {
        setIsLoading(true);

        try{
            const response = await dispatchNumber(phone);
            
            if( response.status === 200 ){
                setIsLoading(false)
                navigate("/verify");
                return;
            }
        } catch(err) {
            alert("An error occured. Try again");
            setIsLoading(false);
            return;
        }
    }

    return (
    <>
        <Flex flexDir="column" h={isSmall ? "100vh" : "85vh"} bgColor="white" w={isSmall ? "100vw" : "60vw"} className={styles.formContainer}>
            <Text mx="8" my="4"  fontSize="3xl" fontWeight="700" color="orange.400" >FoodSub</Text>

            <Box my="8" px="25%" py="8" >
                <Text fontSize="2xl" fontWeight="600" my="6" >Enter your phone number</Text>

                <Input onChange={handleChange} variant="flushed" my="6" mx="2" type='tel' w="60%" placeholder='phone number' />
                <Button onClick={getCode} disabled={phone.length < 11 ? true : false} minW="85px" maxW="85px" colorScheme="orange" >{isLoading ? <Spinner /> : "Get Code"}</Button>
            </Box>
        </Flex>
    </>);
}