import axios from "axios";

const url = "/api/sms/";

export const dispatchNumber = async number => {
    const response = await axios.post(`${url}dispatchCode`, {
        phoneNumber: number
    });

    return response;
}

export const verifyCode = async (number, code) => {
    const response = await axios.post(`${url}verify`, {
        phoneNumber: number,
        code: code
    });

    return response;
}