import { useState } from 'react';
import { Login, Verify } from "./components";
import { Flex } from "@chakra-ui/react";
import {
  BrowserRouter as Router,
  Routes,
  Route
} from "react-router-dom";

function App() {
  const [number, setNumber] = useState("");

  return (
    <Flex flexDir="column" justify="center" align="center" py="9">
      <Router>
        <Routes>
          <Route path="/" element={<Login number={number} setNumber={setNumber} />} />
          <Route path="/verify" element={<Verify number={number} />} />
        </Routes>
      </Router>
    </Flex>
  );
}

export default App;
