import * as React from "react";
import { Flex, Text, Box, Input, Button, useMediaQuery, Spinner } from "@chakra-ui/react";

import { verifyCode } from '../../utils';
import styles from "./verify.module.css";

export default function Verify({ number }){
    const [isSmall] = useMediaQuery("(max-width: 600px)");
    const [isLoading, setIsLoading] = React.useState(false);
    const [code, setCode] = React.useState("");

    const handleChange = event => setCode(event.target.value);
    const checkCode = async () => {
        setIsLoading(true);

        try {
            const response = await verifyCode(number, code);
            
            if( response.status === 200 ){
                await setIsLoading(false);
                alert("Correct!!!");
                return;
            }
        } catch (err) {
            await setIsLoading(false);
            alert("Incorrect code... Try again");
            console.log(err);
            return;	
        }
    }

    return (
    <>
        <Flex flexDir="column" h={isSmall ? "100vh" : "85vh"} bgColor="white" w={isSmall ? "100vw" : "60vw"} className={styles.formContainer}>
            <Text mx="8" my="4"  fontSize="3xl" fontWeight="700" color="orange.400" >FoodSub</Text>

            <Box my="8" px="25%" py="8" >
                <Text fontSize="2xl" fontWeight="600" my="6" >Enter the code sent</Text>

                <Input onChange={handleChange} variant="flushed" my="6" mx="2" type='tel' w="30%" placeholder='code' />
                <Button onClick={checkCode} disabled={code.length !== 4 ? true : false} minW="85px" maxW="85px" colorScheme="orange" >{isLoading ? <Spinner /> : "Verify"}</Button>
            </Box>
        </Flex>
    </>);
}